*, :before, :after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #060b35;
  background-color: #fff;
  font-family: Roboto, sans-serif;
}

.header {
  padding: 20px 40px;
}

.header__nav-list {
  flex-wrap: wrap;
  justify-content: center;
  gap: 24px;
  list-style: none;
  display: flex;
}

.header__nav-link {
  color: #060b35;
  text-decoration: none;
  transition: color .3s;
}

.header__nav-link.is-active {
  opacity: 1;
  letter-spacing: 0;
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 2px;
  width: 37px;
  height: 15px;
  padding: 0 4px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 15px;
  display: inline-block;
  position: relative;
  top: 22px;
  left: 333px;
}

.header__nav-link:hover {
  color: #00acdc;
}

.catalog {
  padding: 50px 40px;
}

.catalog__grid {
  grid-template-columns: 1fr;
  justify-content: center;
  gap: 48px 46px;
  display: grid;
}

@media (width >= 488px) {
  .catalog__grid {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .catalog__grid {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .catalog__grid {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  text-align: center;
  background-color: #fff;
  border-radius: 4px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 16px;
  transition: transform .3s;
  display: flex;
  box-shadow: 0 0 6px #0000000d;
}

.card__image {
  width: 160px;
  max-width: 100%;
  height: 134px;
  margin-bottom: 12px;
}

.card__title {
  margin: 10px 0;
  font-size: 14px;
  font-weight: bold;
  transition: color .3s;
}

.card__code {
  color: #777;
  margin-bottom: 10px;
  font-size: 10px;
}

.card__rating {
  align-items: center;
  gap: 4px;
  margin-bottom: 24px;
  display: flex;
}

.card__star {
  width: 16px;
  height: 16px;
}

.card__reviews {
  color: #777;
  margin-left: 6px;
  font-size: 10px;
}

.card__price-box {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  display: flex;
}

.card__price-label {
  color: #000;
  font-size: 14px;
}

.card__price {
  font-size: 16px;
  font-weight: bold;
}

.card__btn {
  color: #fff;
  cursor: pointer;
  background-color: #00acdc;
  border: none;
  border-radius: 4px;
  width: 100%;
  height: 40px;
  padding: 10px;
  text-decoration: none;
  transition: all .3s;
}

.card:hover {
  z-index: 2;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.card:hover .card__btn {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}
